"use client"

import { Button } from "@/components/ui/button";
import { ArrowRight } from "lucide-react";
import Image from "next/image";
import { motion, useAnimation } from "motion/react";

const GetStartedButton = ({ triggerAnimation }) => {
    return (
        <Button variant="getstarted" size="lg" onClick={triggerAnimation}>
            <span className="">Get started</span>
            <ArrowRight strokeWidth={3.5} className="mb-1" />
        </Button>
    );
};

const PinglePic = () => {
    const controls = useAnimation();

    const handleClick = () => {
        controls.start({
            opacity: [1, 0.8, 1, 0.5, 0],
            x: [0, -5, 5, -3, 3, 0],
            rotate: [0, 2, -2, 1, -1, 0],
            filter: [
                "hue-rotate(0deg)",
                "hue-rotate(20deg)",
                "hue-rotate(-20deg)",
                "hue-rotate(0deg)",
            ],
            transition: { duration: 1.2, ease: "easeInOut" },
        });
    };


    return (
        <motion.div
            initial={{ opacity: 1, x: 0 }}
            animate={controls}
            className="relative z-10 lg:mb-10 w-[60%] md:w-[80%] lg:w-[90%] floating"
        >
            <Image
                src="/pingle_float_2x.webp"
                alt="A floating Pingle"
                width="619"
                height="453"
            />
        </motion.div>
    );
};

export { GetStartedButton, PinglePic };